*, *:before, *:after {
  margin: 0;
  padding: 0;
}
html {
  font-size: 10px;
  font-family: $primary-font;
  box-sizing: border-box;
}
  
body {
  &:before {
    content: '';
    display: block;
    background: $blue-grad;
    height: 15px;
    width: 100%;
  }
}



  


